/*Uses a color palette I generated on https://coolors.co/25283d-add7f6-688b58-efffc8-d16666*/

@import "shared.css";

.content_wrapper {
    overflow: hidden;
}

body::before {
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 105vh;
    background-image: url("../Static/Backgrounds/trianglify.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    overflow: hidden;
    z-index: -100;

    animation: hue_rotate 10s infinite linear alternate;
}

@keyframes hue_rotate {
    0% {
        filter: hue-rotate(-40deg);
    }
    100% {
        filter: hue-rotate(40deg);
    }
}

section {
    padding: 1em;
    margin: 1em;
    color: white;
    border-radius: 10px;
}

h1 {
    color: white;
    text-align: center;
}

.homepage_title {
    font-weight: 900;
}

blockquote + figcaption {
    font-style: italic;
    font-weight: 500;
}

body, main {
    padding: 0;
    margin: 0;
}

/* Engine list styling */
.engine_list {
    color: black;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    padding: 10px;
}

/* The gear icon in the top right corner of the engine article preview */
.engine_list .icon {
    position: absolute;
    right: 1rem;
    top: 1rem;
    opacity: 40%;
    z-index: -1;
}

/* Style the engine logos */
.engine_list .engine_description .engine_logo {
    height: 20vh;

    /* make sure the logo is not too big, but keep the aspect ratio */
    width: auto;
    max-width: 90%;

    object-fit: contain;


    margin-left: auto;
    margin-right: auto;

    background: rgba(255, 255, 255, 0.5);
    padding: 10px;
    border-radius: var(--round_border_width);
}

.engine_list .engine_description {
    display: flex;
    flex-direction: column;
    flex: 1 1 600px;
    min-height: 700px;
    max-width: 30%;
    padding: 1em;
    position: relative;

    box-sizing: border-box;
}

.engine_list .engine_description h2 {
    text-align: center;
}

.engine_page_link {
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: 500;

    /* align to bottom */
    margin: auto 10px 0;

    padding: 20px;
    text-align: center;

    color: black;

    position: relative;

    transition: all 0.3s ease-in-out;
}

/* Add a neon glow to the engine page link */
.engine_page_link:before {
    content: "";
    position: absolute;
    left: 5px;
    top: 5px;
    right: 5px;
    bottom: 5px;
    background: rgba(0, 255, 178, 0.76);
    z-index: -1;
    filter: blur(10px);
    opacity: 1;
    transition: all 0.3s ease-in-out;

    border-radius: var(--round_border_width);
}

.engine_page_link:hover::before {
    left: 10px;
    top: 10px;
    right: 10px;
    bottom: 10px;
}

.engine_page_link::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.2);
    border-radius: var(--round_border_width);

    border: rgba(255, 255, 255, 0.3) 1px solid;
    border-right: rgba(255, 255, 255, 0.15) 1px solid;
    border-bottom: rgba(255, 255, 255, 0.15) 1px solid;

    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease-in-out;
}

.engine_page_link:hover::after {
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.6);
}

.engine_page_link:active::before {
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
}

.engine_page_link:active::after {
    box-shadow: none;
}


.history_articles {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: center;
    gap: 15px;
    color: black;
}

.history_article {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;

    max-width: calc(33% - 10px);

}

/* Add a bright rectangle behind the article */
.history_article:before {
    content: "";
    position: absolute;
    display: block;
    top: 15%;
    left: 25%;
    right: 25%;
    bottom: 15%;
    z-index: -1;
    border-radius: var(--round_border_width);

    background: rgb(255, 98, 0);
    filter: blur(10px);
    opacity: 1;

    transform: scale(1);
    transition: all .5s ease-in-out;
}

.history_article:hover:before {
    top: 10%;
    left: 15%;
    right: 15%;
    bottom: 10%;

    transition: all .75s ease-in-out;
}

#history_before:before {
    background: rgb(0, 155, 49);
}

#history_first:before {
    background: rgb(0, 114, 210);
}

#history_current:before {
    background: rgb(210, 88, 0);
}

.history_article:after {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background: rgba(75, 75, 75, 0.2);
    backdrop-filter: blur(7px) !important;
    color: white;
    border-radius: var(--round_border_width);

    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);


    border: rgba(255, 255, 255, 0.3) 0.1rem solid;
    border-right: rgba(255, 255, 255, 0.15) 0.1rem solid;
    border-bottom: rgba(255, 255, 255, 0.15) 0.1rem solid;
}

.history_img {
    width: 300px;
    height: 400px;
    object-fit: cover;
    border-radius: 1rem;
    margin: 1rem;
}

.history_content {
    padding: 2em;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1em;
    color: white;

    transition: all 0.5s ease;

    height: 100%;
}

.history_content h2 {
    font-size: 1.5rem;
    font-weight: 500;
    margin: 0;
}

.history_button_wrapper a{
    text-decoration: none;
    padding: .5em;
    background: white;
    color: black;

    font-weight: 600;

    border-radius: 10px;
    font-size: 1.5rem;

    transition: all 0.3s ease-in-out;

    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.5);
}

.history_button_wrapper .history_link:hover {
    background: #c9c9c9;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.6);
}

.history_button_wrapper .history_link:active {
    background: var(--light-red);
    box-shadow: none;
}

.history_button_wrapper > div {
    transition: all 0.3s ease-in-out;

    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.5);
}

.history_button_wrapper > div:hover {
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.6);
}

.history_button_wrapper > div:active {
    box-shadow: none;
}

.history_button_wrapper {

    margin-top: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    flex-wrap: wrap;
    gap: 1rem;
}

.history_button_wrapper button {
    margin: 0;
    border: 0;
    vertical-align: baseline;

    text-decoration: none;
    padding: .5em;
    background: white;
    color: black;

    box-sizing: content-box;

    font-weight: 600;

    border-radius: 10px;
    font-size: 1.5rem;

    transition: all 0.3s ease-in-out;
}

.article_like_wrapper {
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    box-shadow: none;

    background: white;
    border-radius: var(--round_border_width);

    cursor: pointer;

    transform: scale(1);
    transition: all 0.5s ease;
}

.article_like_wrapper:hover {
    transform: scale(1.15);
}

.article_like_wrapper .article_like {
    position: relative;
    height: 1.5rem;

    margin-left: 10px;

    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.article_like_wrapper i {
    font-size: 1.5rem;
    color: gray;
    transition: 0.3s all;
}

/* Display a white circle behind the icon */
.article_like_wrapper .article_like:before {
    content: "";
    position: absolute;
    border-radius: 50%;
    background: transparent;
    left: 4px;
    top: 4px;
    right: 4px;
    bottom: 4px;
    z-index: -1;

    transform: scale(1);
    transition: transform 0.2s ease-in-out, filter 0.2s ease-in-out, background 0.5s ease;
}

.article_like_icon_clicked i {
    color: white;
    font-size: 1.5rem;
    margin-top: 0.25rem;

    transform: scale(1);
    transition: transform 0.2s ease-in-out, filter 0.2s ease-in-out;
}

.article_like_wrapper .article_like_icon_clicked:before {
    background: var(--light-red);
}

.article_like_wrapper .like_count {
    color: black;
    font-weight: 700;
    font-size: 1.5rem;
    margin-right: 20px;
}

.article_like.article_like_icon_clicked_temp .article_like:before, .article_like_icon_clicked.article_like_icon_clicked_temp:before,
.article_like_icon_clicked.article_like_icon_clicked_temp i {
    /* make like icon bigger */
    transform: scale(1.3);
    filter: saturate(2) brightness(1.5);
}

/* Style history articles */
@media screen and (max-width: 1700px) {

    .history_articles {
        flex-direction: column;
        align-content: center;
    }

    .history_article {
        max-width: calc(50% - 10px);
    }
}

@media screen and (max-width: 1300px) {
    .history_article {
        max-width: calc(80% - 10px);
    }
}

@media screen and (max-width: 800px) {
    .history_article {
        max-width: 100%;
    }

    .history_article img {
        width: calc(300px * 0.75);
        height: calc(400px * 0.75);
    }
}

@media screen and (max-width: 550px) {
    .history_article {
        flex-direction: column-reverse;
    }

    .history_article img {
        width: 80%;
        max-width: 300px;
        height: auto;
        aspect-ratio: 3/4;
    }
}

/* Style engines two column layout */
@media screen and (max-width: 1350px) {

    .main {
        padding: 0 .5rem;
    }

    .article_link_wrapper {
        width: 60%;
    }

    /* Engine list styling on small width */
    .engine_list {
        padding: 0;
        gap: 10px;
    }

    .engine_list .engine_description {
        max-width: 49%;
        min-height: 600px;
    }

}


@media screen and (max-width: 1200px) {
    .articles {
        flex-direction: column;
        gap: 2rem;
    }
}

@media screen and (max-width: 1000px) {

    /* Engine list styling on very small width */
    .engine_list {
        flex-direction: column;
        align-items: center;
        padding: 0;
        gap: 1rem;
        box-sizing: border-box;
    }

    .engine_list .engine_description {
        max-width: 100%;
        width: 100%;
        min-height: 500px;
        flex-basis: auto;
    }

    .engine_section {
        margin-left: 0;
        margin-right: 0;
    }
}

@media screen and (max-width: 600px) {

    .article_link_wrapper {
        width: 80%;
    }
}